/* Reset and global styles */
body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Navbar base */
.navbar {
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  backdrop-filter: blur(10px);
}

/* Logo container */
.logo-container {
  display: flex;
  align-items: center;
}

/* Logo image */
.logo {
  height: 1.6em;
  margin-right: 10px;
  margin-left: 20px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #1976d2;
  box-shadow: 0 0 5px #1976d2;
  transition: height 0.3s ease, margin-right 0.3s ease, font-size 0.3s ease, transform 0.3s ease; /* Added transform */
  transform: translateY(0); /* Default: no vertical shift */
}

/* Logo text button */
.logo-button {
  font-size: 1.1em;
  font-weight: bold;
  color: white;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: color 0.3s ease, font-size 0.3s ease, transform 0.3s ease; /* Added transform */
  outline: none;
  line-height: 1;
  display: flex;
  align-items: center;
  transform: translateY(0); /* Default: no vertical shift */
}

.logo-button:hover {
  color: #1976d2;
}

/* Nav links */
.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-size: 1em;
  transition: color 0.3s ease;
  font-family: 'Montserrat Condensed', sans-serif;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.nav-links a:hover {
  color: #1976d2;
}

/* Hamburger menu icon */
.hamburger {
  display: none;
  font-size: 1.8em;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  z-index: 1100;
  margin-top: -35px;
}

/* Mobile responsive adjustments */
@media screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 20px;
  }

  .hamburger {
    display: block;
    align-self: flex-end;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-top: 10px;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-links li {
    width: 100%;
    text-align: left;
    padding: 8px 10px;
  }

  .logo {
    height: 1.4em; /* Increase this to make the logo image larger */
    transform: translateY(2px); /* Adjust vertical alignment */
  }
  
  .logo-button {
    font-size: 1em; /* Increase this to make the logo text bigger */
    transform: translateY(2px); /* Adjust vertical alignment */
  }
}
